// Services s√£o os "servi√ßos" que podem ser reutilizados por todo o app
// Geralmente s√£o fun√ß√µes
// Service para fun√ß√µes relacionadas ao Dispositivo

import { Injectable } from '@angular/core';
// Importa√ß√£o das bibliotecas do AngularFire
import { AngularFireDatabase } from '@angular/fire/database';

@Injectable({
    providedIn: 'root'
})

export class UserService {
    // Declara√ß√£o das vari√°veis
    name: any;

    constructor(
        // AngularFireDatabase √© uma classe para refernciar o banco
        // Recebeu o apelido de db
        private db: AngularFireDatabase
    ) {

    }

    // Fun√ß√£o que pega o nome do usu√°rio
    getUserName(email) {
        email = email.replace(/[.#$@]+/g, '');
        this.name = this.db.object(`/users/${email}/settings/displayName`);
        return this.name;
    }

    // Fun√ß√£o para mudar o nome do usu√°rio, com a fun√ß√£o √© passado o parametro name, fornecido pela fun√ß√£o
    updateUserName(name, email) {
        email = email.replace(/[.#$@]+/g, '');
        this.db.database.ref(`/users/${email}/settings/displayName`).set(name);
    }
}
