import { Component, OnInit } from '@angular/core';
import { User } from 'src/app/models/user.model';
import { ActionSheetController, AlertController, ToastController } from '@ionic/angular';
import { UserService } from 'src/app/services/user.service';
import { AuthenticationService } from '../../services/authentication.service';

@Component({
  selector: 'app-menu',
  templateUrl: './menu.page.html',
  styleUrls: ['./menu.page.scss'],
})
export class MenuPage implements OnInit {

  // Vari√°vel user
  user: User;
  novoNome: any;
  name: any;
  photo: any;

  pages = [
    {
      title: 'Home',
      url: '/menu/home',
      icon: 'home'
    },
    {
      title: 'Editar',
      icon: 'create-outline',
      open: false,
      children: [
        {
          title: 'Dispositivo',
          url: '/menu/edit-device-list',
          icon: 'hardware-chip'
        },
        {
          title: 'C√¥modo',
          url: '/menu/edit-room-list',
          icon: 'home'
        }
      ]
    },
    {
      title: 'Fun√ß√µes',
      icon: 'build',
      open: false,
      children: [
        {
          title: 'Timer',
          url: '/menu/device-list',
          icon: 'alarm-outline'
        },
      ]
    },
  ];

  constructor(
    private actionSheetCtrl: ActionSheetController,
    private alertCtrl: AlertController,
    private toastController: ToastController,
    private userService: UserService,
    private authService: AuthenticationService,
  ) { }

  ngOnInit() { }

  async ionViewWillEnter() {
    await this.userName();
  }

  userName() {
    this.user = JSON.parse(localStorage.getItem('user'));

    if (this.user.displayName === null && this.user.photoURL === null) {
      // tslint:disable-next-line: max-line-length
      localStorage.setItem('user', JSON.stringify(new User(this.user.uid, this.user.email, this.user.email, this.user.emailVerified, 'https://upload.wikimedia.org/wikipedia/commons/8/89/Portrait_Placeholder.png')));
      this.user = JSON.parse(localStorage.getItem('user'));
      this.userService.updateUserName(this.user.displayName, this.user.email);
    } else {
      this.userService.getUserName(this.user.email).valueChanges().subscribe(res => {
        this.user.displayName = res;
        // tslint:disable-next-line: max-line-length
        localStorage.setItem('user', JSON.stringify(new User(this.user.uid, this.user.email, this.user.displayName, this.user.emailVerified, this.user.photoURL)));
      });
    }
    this.user = JSON.parse(localStorage.getItem('user'));
    this.name = this.user.displayName;
    this.photo = this.user.photoURL;
  }


  // Fun√ß√£o para aparecer op√ß√µes ao clicar no usu√°rio
  async showOptions() {
    // Cria um actionSheet
    const actionSheet = await this.actionSheetCtrl.create({
      // Header com nome 'Op√ß√µes'
      header: 'Op√ß√µes',
      cssClass: 'actionSheet',
      // Gera bot√µes
      buttons: [
        {
          text: 'Alterar Nome',
          icon: 'person',
          handler: () => {
            this.alterarNome();
          }
        },
        {
          text: 'Logout',
          icon: 'power',
          role: 'destructive',
          // Quando o Logout √© acionado ele remove o usu√°rio do local storage e redireciona para a p√°gina de login
          handler: () => {
            this.presentAlertConfirm();
          }
        }, {
          // Bot√£o para cancelar
          text: 'Cancelar',
          icon: 'close',
          role: 'cancel',
        }]
    });
    // Aguarda o actionSheet carregar antes de mostrar
    await actionSheet.present();
  }

  // Func√£o para aparecer o alert com o input que ir√° receber o novo nome do usu√°rio.
  async alterarNome() {
    // Cria um alert
    const alert = await this.alertCtrl.create({
      // Header com nome 'Novo Nome'.
      header: 'Novo Nome',
      // Gera inputs
      inputs: [
        {
          name: 'new-name',
          id: 'newname',
          placeholder: 'Escreva um novo nome',
          value: this.novoNome
        },
      ],
      // Gera bot√µes
      buttons: [
        {
          // Bot√£o para cancelar
          text: 'Cancelar',
          role: 'cancel',
        },
        {
          text: 'Salvar',
          handler: data => {
            // if apenas para saber se o input n√£o est√° v√°zio.
            if ((document.getElementById('newname') as HTMLInputElement).value !== '') {
              // this.nome ser√° = ao que est√° valor do input com Id 'newname'.
              this.novoNome = (document.getElementById('newname') as HTMLInputElement).value;
              // Puxa a fun√ß√£o do ion-toast.
              this.newname();
              // Fun√ß√£o para mudar o nome do usu√°rio.
              this.userService.updateUserName(this.novoNome, this.user.email);
              // tslint:disable-next-line: max-line-length
              localStorage.setItem('user', JSON.stringify(new User(this.user.uid, this.user.email, this.novoNome, this.user.emailVerified, this.user.photoURL)));
              this.user = JSON.parse(localStorage.getItem('user'));
              this.name = this.user.displayName;
            }
          }
        }
      ]
    });
    await alert.present();
  }

  // Fun√ß√£o para aparecer um ion-toast ap√≥s salvar o novo nome (perfumaria).
  async newname() {
    const toast = await this.toastController.create({
      header: 'Seu nome foi alterado.',
      duration: 2000
    });
    await toast.present();
  }

  async presentAlertConfirm() {
    const alert = await this.alertCtrl.create({
      header: 'SAIR!',
      message: 'Deseja realmente sair?',
      buttons: [
        {
          text: 'Cancelar',
          role: 'cancel'
        }, {
          text: 'Confirmar',
          handler: () => {
            this.authService.SignOut();
          }
        }
      ]
    });

    await alert.present();
  }

}
